import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams,MenuController } from 'ionic-angular';
import { Events } from 'ionic-angular';
import { FormBuilder, FormGroup, Validators, AbstractControl } from '@angular/forms';

import { RestProvider } from '../../providers/rest/rest';

import { Storage } from '@ionic/storage';
import { AlertController } from 'ionic-angular';

import { LoadingController } from 'ionic-angular';

/**
 * Generated class for the LoginPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  constructor(public navCtrl: NavController, public navParams: NavParams,private menu: MenuController,
  public formBuilder: FormBuilder,public events: Events,public loadingCtrl: LoadingController, public storage: Storage,public alertCtrl: AlertController, public apiProvider: RestProvider) {
     this.menu.swipeEnable(false);




             this.authForm = formBuilder.group({
           // username: ['', Validators.compose([Validators.required, Validators.pattern('[a-zA-Z]*'), Validators.minLength(5), Validators.maxLength(15)])],
                username: ['', Validators.compose([Validators.required])],

            password: ['', Validators.compose([Validators.required])]
        });


  }

    ionViewDidEnter() {
    this.menu.swipeEnable(false);

    // If you have more than one side menu, use the id like below
    // this.menu.swipeEnable(false, 'menu1');
  }
  

  ionViewDidLoad() {
    console.log('ionViewDidLoad LoginPage');
  }


 ionViewWillLeave() {
    // Don't forget to return the swipe to normal, otherwise 
    // the rest of the pages won't be able to swipe to open menu
    this.menu.swipeEnable(true);

    // If you have more than one side menu, use the id like below
    // this.menu.swipeEnable(true, 'menu1');
   }



    presentAlert(titulo,mensaje) {
    let alert = this.alertCtrl.create({
      title: titulo,
      subTitle: mensaje,
      buttons: ['Cerrar']
    });
    alert.present();
  }


   doLogin(data) {

    let loading = this.loadingCtrl.create({content : "Verificando Credenciales..."});
  loading.present();
console.log(data);
    this.apiProvider.doLoginApiAE(data)
    .then(data => {    
        console.log(data);
      if(data.data[0] && (data.data[0].idEmpleado  > 0)){
   
        this.storage.get('pushKeyAEBYST').then((value) => {
        if(value){
        console.log(value);
                var pushState = { 
                pushK:value, 
                device: device.platform,
                deviceId: device.uuid,
                login: Date.now(),
                user: data.data[0].idEmpleado
                }
                console.log(pushState);
              this.apiProvider.addPush2(pushState).then(data => {
              console.log(data); 
              });
        }
        });

   
     

       // this.dataProfile = data.data[0];
        this.storage.set(`usr_tok_byae`, data.data[0]);
        this.events.publish('loginOK', data.data[0]);

        if(data.data[0].horarioSet == 1){

           this.apiProvider.horarioSet({idEmpleado : data.data[0].idEmpleado})
    .then(datax => {   console.log(datax)});

        this.navCtrl.setRoot('HorarioPage', {first: 1});
        this.storage.set(`usr_alrt_byae`, true);
        }
        else{
        this.navCtrl.setRoot('CalendarioPage');
        this.storage.set(`usr_alrt_byae`, false);
        }
        
        
        //this.events.publish('userCreated', data.data[0]);
        

        loading.dismiss();
		//this.navCtrl.push('CalendarioPage');
		
        //this.closeModal();


      
      
      }
      else{
      loading.dismiss();
      this.presentAlert('Ups!','Credenciales incorrectas');
      }

    });

  }






}



